}

:root {
  --main-font: "Roboto", sans-serif;
  --main-font-color: #000;
  --second-font-color: #8c8c8c;
  --accent-font-color: #ff6b08;
  --header-font-color: #545454;
  --main-light-color: #fff;
  --main-dark-color: #000;
  --main-accent-color: #ff6b01;
  --main-gray-color: #f7f7f7;
  --placeholder-color: rgba(255, 255, 255, .8);
  --header-background-color: rgba(0, 0, 0, .56);
  --card-box-shadow: 0px 0px 8px rgba(255, 107, 1, .9);
  --btn-box-shadow: 0px 8px 43px rgba(255, 107, 1, .6);
  --grid-items: 1;
  --grid-indent: 20px;
  --animation: 250ms cubic-bezier(.4, 0, .2, 1);
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: var(--main-font);
  color: var(--main-font-color);
}

.no-scroll {
  overflow: hidden;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
  display: block;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.icon {
  fill: currentColor;
  stroke: currentColor;
}

.link {
  text-decoration: none;
}

.link:hover, .link:focus {
  outline-color: var(--main-accent-color);
}

.title {
  letter-spacing: .03em;
  font-size: 28px;
  font-weight: 700;
  line-height: 1.17;
}

@media screen and (min-width: 1280px) {
  .title {
    font-size: 36px;
  }
}

.subtitle {
  margin-bottom: 10px;
}

.grid {
  gap: var(--grid-indent);
  flex-wrap: wrap;
}

@media screen and (min-width: 768px) {
  .grid {
    gap: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .grid {
    gap: 32px 16px;
  }
}

.grid__item {
  flex-basis: 100%;
}

@media screen and (min-width: 768px) {
  .grid__item {
    flex-basis: calc(50% - 16px);
  }
}

@media screen and (min-width: 1280px) {
  .grid__item {
    flex-basis: calc(33.3333% - 10.6667px);
  }
}

.backdrop {
  width: 100%;
  height: 100%;
  background: var(--backdrop-background);
  opacity: 1;
  visibility: visible;
  transition: visibility var(--animation), opacity var(--animation);
  position: fixed;
  top: 0;
  left: 0;
}

.button {
  min-width: 130px;
  text-align: center;
  text-transform: uppercase;
  color: var(--main-light-color);
  border: 1px solid var(--main-light-color);
  transition: color var(--animation), background-color var(--animation), box-shadow var(--animation);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding: 12px 20px;
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
  display: inline-flex;
}

.button:hover, .button:focus {
  border-color: var(--main-accent-color);
  background-color: var(--main-accent-color);
  box-shadow: var(--btn-box-shadow);
  cursor: pointer;
}

.button--active {
  border-color: var(--main-accent-color);
  background-color: var(--main-accent-color);
  box-shadow: var(--btn-box-shadow);
}

.container {
  min-width: 280px;
  max-width: 480px;
  margin: 0 auto;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
  }
}

.container--flex {
  justify-content: center;
  align-items: center;
  display: flex;
}

.navigation {
  flex-shrink: 0;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.navigation__list {
  gap: 40px;
}

.navigation__link {
  text-align: center;
  text-transform: uppercase;
  color: var(--main-light-color);
  transition: color var(--animation);
  padding: 3px 1px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  position: relative;
}

.navigation__link:hover, .navigation__link:focus {
  color: var(--main-accent-color);
}

.navigation__link--current:after {
  width: 100%;
  height: 3px;
  content: "";
  background-color: var(--main-accent-color);
  position: absolute;
  bottom: 0;
  left: 0;
}

@media screen and (min-width: 768px) {
  .logo {
    color: var(--main-light-color);
    transition: color var(--animation);
    align-items: center;
    gap: 10px;
    display: flex;
  }
}

.logo:hover, .logo:focus {
