.scroll-progress {
  width: 65px;
  height: 65px;
  cursor: pointer;
  color: var(--main-light-color);
  z-index: 999;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  bottom: 20px;
  right: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, .6);
}

.scroll-progress__value {
  height: calc(100% - 20px);
  width: calc(100% - 20px);
  background-color: var(--header-font-color);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-size: 30px;
  display: flex;
}

